import React from 'react';

function YesNoPopup({title, onYes, onNo}: { title: String, onYes: Function, onNo: Function }) {
   const buttonStyle = 'w-20 justify-center rounded-md bg-white text-gray-900 ' +
      'shadow-sm ring-inset ring-1 ring-gray-300 my-1.5';
   const handleCancel = () => {
      onNo();
   };
   
   const handleConfirm = () => {
      onYes();
   };
   return (
      <div className='fixed top-0 left-0 z-10 w-full h-full bg-black bg-opacity-40 flex items-center justify-center'>
         <div className="w-80 h-40 bg-white rounded-lg flex items-center justify-center flex-col">
            <div className='w-full px-4 flex-auto flex items-center justify-center'>
               <p className='text-center overflow-hidden'>{title}</p>
            </div>
            <div className='w-full h-12 flex justify-center gap-2 rounded-b-lg'>
               <button className={buttonStyle} onClick={handleCancel}>아니오</button>
               <button className={buttonStyle} onClick={handleConfirm}>예</button>
            </div>
         </div>
      </div>
   );
}

export default YesNoPopup;