import React, {useState} from 'react';
import {PhotoIcon, UserCircleIcon} from '@heroicons/react/24/solid'
import {baseUrl, inputBoxStyle, inputLabelStyle} from "../constants/constants";
import axios from "axios";
import {User} from "../constants/models";
import {useNavigate} from "react-router-dom";
import YesNoPopup from "../components/YesNoPopup";
import InstallPrompt from "../components/InstallPrompt";

function SignUp() {
  const [id, setId] = useState('');
  const [pw, setPw] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [address, setAddress] = useState('');
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  
  const handleSignUp = (e: any) => {
    e.preventDefault();
    const body = {
      id, pw, name, email, address
    };
    
    axios.post(baseUrl + '/users/signup', body)
       .then(response => {
         console.log(response.data);
         const _user = new User(response.data);
         alert(`${_user.userid}님 가입되었습니다.`);
         navigate('/');
         // setTimeout(args => (
         //    navigate('/')
         // ), 0);
         
       })
       .catch(reason => {
         console.log(reason);
         alert(`가입에 실패했습니다.\n${reason}`);
       })
  }
  
  const handleCancelClick = () => {
    setShowModal(true);
  };
  
  const handlePopupYes = () => {
    setShowModal(false);
    navigate('/');
  };
  
  const handlePopupNo = () => {
    setShowModal(false);
  };
  
  return (
     <>
       <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
         <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm lg:max-w-lg lg:mx-auto">
           <p className='font-bold text-gray-900 border-b border-gray-900/10 pb-5'>회원가입</p>
           <form onSubmit={handleSignUp}>
             <div className="border-b border-gray-900/10 pb-12">
               <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                 {/*ID*/}
                 <div className="sm:col-span-full">
                   <label htmlFor="id" className={inputLabelStyle}>ID</label>
                   <div className="mt-2">
                     <input
                        id="id"
                        name="id"
                        type="text"
                        required
                        value={id}
                        onChange={(e) => setId(e.target.value)}
                        className={inputBoxStyle}
                     />
                   </div>
                 </div>
                 {/*PW*/}
                 <div className="sm:col-span-full">
                   <label htmlFor="pw" className={inputLabelStyle}>Password</label>
                   <div className="mt-2">
                     <input
                        id="pw"
                        name="pw"
                        type="password"
                        required
                        value={pw}
                        onChange={(e) => setPw(e.target.value)}
                        className={inputBoxStyle}
                     />
                   </div>
                 </div>
                 {/*Name*/}
                 <div className="sm:col-span-full">
                   <label htmlFor="name" className={inputLabelStyle}>Name</label>
                   <div className="mt-2">
                     <input
                        type="text"
                        id="name"
                        name="name"
                        required
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        className={inputBoxStyle}
                     />
                   </div>
                 </div>
                 
                 {/*Email*/}
                 <div className="sm:col-span-full">
                   <label htmlFor="email" className={inputLabelStyle}>Email</label>
                   <div className="mt-2">
                     <input
                        id="email"
                        name="email"
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        className={inputBoxStyle}
                     />
                   </div>
                 </div>
                 
                 {/*Address*/}
                 <div className="sm:col-span-full">
                   <label htmlFor="address" className={inputLabelStyle}>Address</label>
                   <div className="mt-2">
                     <input
                        id="address"
                        name="address"
                        value={address}
                        onChange={(e) => setAddress(e.target.value)}
                        className={inputBoxStyle}
                     />
                   </div>
                 </div>
               </div>
             </div>
             <div className="mt-6 flex items-center justify-end gap-x-6">
               <button type="button" 
                       className="text-sm font-semibold leading-6 text-gray-900 px-3 py-1.5 w-20 rounded-lg ring-1 ring-inset ring-gray-300"
                       onClick={handleCancelClick}>
                 취소
               </button>
               <button
                  type="submit"
                  className="rounded-md bg-indigo-600 px-3 py-2 w-20 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 
               focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
               >회원가입
               </button>
             </div>
           </form>
         </div>
       </div>
       {showModal && (
          <YesNoPopup title='취소하시겠습니까?' onYes={handlePopupYes} onNo={handlePopupNo} />
       )}
     </>
  )
}

export default SignUp;