import React, {useEffect, useState} from 'react';
import {useUserStore} from "../stores/users";
import axios from "axios";
import {baseUrl, boardLink, inputLabelStyle, okButtonStyle} from "../constants/constants";
import {Board} from "../constants/models";
import {Link, useNavigate} from "react-router-dom";

export const boardsList: Board[] = [
   {
      id: 1,
      title: '테스트1',
      contents: '내용',
      date: new Date(2024, 3, 1),
      writerid: 1,
      writername: '관리자',
   },
   {
      id: 2,
      title: '테스트2',
      contents: '내용',
      date: new Date(2024, 3, 2),
      writerid: 1,
      writername: '관리자',
   },
];


function BoardList() {
   const {user} = useUserStore();
   const [boardList, setBoardList] = useState<Board[]>([]);
   const navigate = useNavigate();
  
   const getBoardList = async () => {
      // const res = (await axios.get(baseUrl + '/boards')).data;
      // console.log(res);
      // setBoardList(res);
      setBoardList(boardsList);
   };
   
   useEffect(() => {
      getBoardList();
   }, []);
   
   const onCreateClick = async () => {
      // axios.post(baseUrl + '/boards/create', {writerid: user.id})
      //    .then(res => {
      //       const _board = new Board(res.data);
      //       navigate(`${boardLink}/${_board.id}`, {state: {edit: true}});
      //    })
      //    .catch(reason => {
      //       console.log(reason);
      //    });
      
      const _board = new Board({
         id: boardList.length + 1,
         title: '',
         contents: '',
         date: new Date(),
         writerid: 1,
         writername: '관리자',
      });
      
      boardList.push(_board);
      navigate(`${boardLink}/${_board.id}`, {state: {edit: true}});
      
   }
   
   return (
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen '>
            <p className={`${inputLabelStyle} font-bold mb-2`}>게시판 목록</p>
         </div>
         <div className='sm:mx-2 lg:max-w-screen lg:mx-[100px] border divide-y'>
            <div className="grid gap-x-6 gap-y-2 sm:grid-cols-6 lg:grid-cols-6 lg:w-full">
               <div className="md:col-span-1 md:border-r ">번호</div>
               <div className="md:col-span-3 md:border-r">제목</div>
               <div className="md:col-span-1 md:border-r">작성자</div>
               <div className="md:col-span-1">작성일</div>
            </div>
            {boardList.map((board) => {
               const koreanDate = new Date(board.date.toLocaleString("en-US", {timeZone: "Asia/Seoul"}));
               const formatted = `${koreanDate.getFullYear()}/${koreanDate.getMonth() + 1}/${koreanDate.getDate()}`;
               return (
                  <Link key={board.id} to={`${boardLink}/${board.id}`}>
                     <div className="grid gap-x-6 gap-y-2 sm:grid-cols-6 lg:grid-cols-6 lg:w-full">
                        <div className="md:col-span-1 md:border-r">{board.id}</div>
                        <div className="md:col-span-3 md:border-r">{board.title}</div>
                        <div className="md:col-span-1 md:border-r">{board.writername}</div>
                        <div className="md:col-span-1">{formatted}</div>
                     </div>
                  </Link>
               );
            })}
         </div>
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen mt-2 flex justify-end'>
            <button className={`${okButtonStyle} w-32`} onClick={onCreateClick}>추가</button>
         </div>
      </div>
   );
}

export default BoardList;