import React, {MouseEventHandler, useEffect, useState} from 'react';
import {baseUrl, boardLink, inputBoxStyle, inputLabelStyle, okButtonStyle} from "../constants/constants";
import axios from "axios";
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import {Board, User} from "../constants/models";
import {boardsList} from "./BoardList";

function BoardDetail() {
   const {id} = useParams();
   const [loading, setLoading] = useState(true);
   const [board, setBoard] = useState<Board | null>(null);
   const [edit, setEdit] = useState(false);
   const navigate = useNavigate();
   const location = useLocation();
   
   const getBoard = async () => {
      if (!id) return;
      
      const body = {id: parseInt(id)};
      axios.post(baseUrl + '/boards/find', body)
         .then(res => {
            const _board = new Board(res.data);
            setBoard(_board);
            
         })
         .catch(reason => {
            console.log(reason);
         })
         .finally(() => {
            setLoading(false);
         });
   };
   
   useEffect(() => {
      getBoard();
      setEdit(location.state != null ? location.state!.edit : false);
   }, []);
   
   useEffect(() => {
      console.log(board);
   }, [board]);
   
   const onEditClick = () => {
      setEdit(true);
   }
   
   const onCreateClick = (board: Board) => {
      setEdit(false);
      
      const body = {
         id: board.id,
         title: board.title,
         contents: board.contents,
      };
      
      // axios.post(baseUrl + '/boards/update', body)
      //    .then(res => {
      //       const _board = new Board(res.data);
      //       setBoard(_board);
      //    })
      //    .catch(reason => {
      //       console.log(reason);
      //    })
      //    .finally(() => {
      //       setLoading(false);
      //       onListClick();
      //    });
      
      setLoading(false);
      onListClick();
   };
   
   const onListClick = () => {
      navigate(boardLink);
   };
   
   return (
      <div>
         {loading
            ? (<h2>loading...</h2>)
            : (
               edit
                  ? <DetailEdit board={board!} onCreateClick={onCreateClick} onListClick={onListClick}/>
                  : <DetailView board={board!} onEditClick={onEditClick} onListClick={onListClick}/>
            )
         }
      </div>
   );
}

function DetailView({board, onEditClick, onListClick}: {
   board: Board,
   onEditClick: MouseEventHandler<HTMLButtonElement>,
   onListClick: MouseEventHandler<HTMLButtonElement>
}) {
   
   return (
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen'>
            <p className={`${inputLabelStyle} font-bold mb-2`}>게시판 내용</p>
         </div>
         <div className='sm:mx-2 lg:max-w-screen lg:mx-[100px] border divide-y'>
            <div className="grid grid-cols-6 lg:w-full">
               <div className="col-span-1 px-2 border-r border-b">제목</div>
               <div className="col-span-5 px-2 border-b">{board!.title}</div>
               
               <div className="col-span-1 px-2 border-r border-b">작성자</div>
               <div className="col-span-2 px-2 border-r border-b">{board!.writername}</div>
               <div className="col-span-1 px-2 border-r border-b">작성일</div>
               <div className="col-span-2 px-2 border-b">{setDate(board!.date)}</div>
               
               <div className="col-span-full px-2 py-2 h-80">{board!.contents}</div>
            </div>
         </div>
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen mt-2 flex justify-end'>
            <button className={`${okButtonStyle} w-32 mr-2`} onClick={onListClick}>목록</button>
            <button className={`${okButtonStyle} w-32`} onClick={onEditClick}>수정</button>
         </div>
      </div>
   );
}

function DetailEdit({board, onCreateClick, onListClick}: {
   board: Board, onCreateClick: Function,
   onListClick: MouseEventHandler<HTMLButtonElement>
}) {
   const [title, setTitle] = useState(board!.title);
   const [contents, setContents] = useState(board!.contents);
   
   const handleTitleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setTitle(event.target.value);
      board.title = event.target.value;
   };
   
   const handleContentsChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
      setContents(event.target.value);
      board.contents = event.target.value;
   };
   
   const _onCreateClick = () => {
      onCreateClick(board);
   }
   
   return (
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen '>
            <p className={`${inputLabelStyle} font-bold mb-2`}>게시판 내용</p>
         </div>
         <div className='sm:mx-2 lg:max-w-screen lg:mx-[100px] border divide-y'>
            <div className="grid grid-cols-6 lg:w-full">
               <div className="col-span-1 px-2 border-r border-b">제목</div>
               <div className="col-span-5 border-b">
                  <input className={inputBoxStyle} type='text' value={title} onChange={handleTitleChange}/>
               </div>
               
               <div className="col-span-1 px-2 border-r border-b">작성자</div>
               <div className="col-span-2 px-2 border-r border-b">{board!.writername}</div>
               <div className="col-span-1 px-2 border-r border-b">작성일</div>
               <div className="col-span-2 px-2 border-b">{setDate(board!.date)}</div>
               
               <div className="col-span-full h-80">
                  <textarea className={`${inputBoxStyle} w-full h-full resize-none`} value={contents} onChange={handleContentsChange}/>
               </div>
            </div>
         </div>
         <div className='sm:mx-2 lg:mx-[100px] lg:max-w-screen mt-2 flex justify-end'>
            <button className={`${okButtonStyle} w-32 mr-2`} onClick={onListClick}>목록</button>
            <button className={`${okButtonStyle} w-32`} onClick={_onCreateClick}>저장</button>
         </div>
      </div>
   );
}

const setDate = (date: Date): string => {
   const koreanDate = new Date(date.toLocaleString("en-US", {timeZone: "Asia/Seoul"}));
   const formatted = `${koreanDate.getFullYear()}/${koreanDate.getMonth() + 1}/${koreanDate.getDate()}`;
   
   return formatted;
}

export default BoardDetail;