export class User {
  constructor(obj: any) {
    this.id = obj.id;
    this.userid = obj.userid;
    this.pw = obj.pw;
    this.name = obj.name;
    this.email = obj.email;
    this.address = obj.address;
    this.phone = obj.phone;
    this.fbtoken = obj.fbtoken;
  }
  id: number = 0;
  userid: string = '';
  pw: string = '';
  name: string = '';
  email: string = '';
  address: string = '';
  phone: string = '';
  fbtoken: string = '';
}

export class Board {
  constructor(obj: any) {
    this.id = obj.id;
    this.title = obj.title;
    this.contents = obj.contents;
    this.date = obj.date;
    this.writerid = obj.writerid;
    this.writername = obj.writername;
  }
  id: number = 0;
  title: string = '';
  contents: string = '';
  date: Date = new Date(2024);
  writerid: number = 0;
  writername: string = '';
}